import React from "react";
import { useHistory } from "react-router-dom";
import { useState } from "react";

// connect requests with axios
import axios from "axios";

// reactstrap components
import {
  Button,
  Card,
  CardHeader,
  CardBody,
  CardTitle,
  CardFooter,
  Label,
  Form,
  FormGroup,
  Input,
  Col,
  Alert
} from "reactstrap";

import { User } from "../backend-sdk/user.sdk";
import { Enviroments } from "../enviroments/enviroments";

function Login(props) {
  const history = useHistory();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState(null);
  const [isSubmitting, setIsSubmitting] = useState(false);

  async function handleSubmit(event) {
    setError(null);

    event.preventDefault();
    if (!email || !password) {
      setError("Todos os campos so obrigatrios");
      console.log("All fields are mandatory");
      return;
    }

    setIsSubmitting(true);

    const loginData = {
      email,
      password
    };

    try {
      const res = await axios.post(`${Enviroments.API_URL}auth`, loginData);
      localStorage.setItem("apiToken", res.data.token);
      localStorage.setItem("user", JSON.stringify(loginData));
      localStorage.setItem("roles", res.data.roles);
      localStorage.setItem("id", res.id);
      history.push("/admin/dashboard");
    } catch (err) {
      setError("Usurio ou senha invalido");
      console.log(err.error);
      setIsSubmitting(false);
      return;
    }
  }

  return (
    <div>
      <Col className="ml-auto mr-auto col-md-6 col-lg-4">
        <Card>
          <Form>
            <CardHeader>
              <CardTitle tag="h3">Login</CardTitle>
            </CardHeader>
            <CardBody>
              <Alert isOpen={error != null} color="danger">
                {error}
              </Alert>
              <FormGroup>
                <Label>Email</Label>
                <Input
                  defaultValue="Write your Email here"
                  placeholder="Email"
                  type="email"
                  autoComplete="email"
                  value={email}
                  onChange={e => setEmail(e.target.value)}
                />
              </FormGroup>

              <FormGroup>
                <Label>Password</Label>
                <Input
                  defaultValue="Write your password here"
                  placeholder="Password"
                  type="password"
                  autoComplete="password"
                  value={password}
                  onChange={e => setPassword(e.target.value)}
                />
              </FormGroup>

              <a
                style={{ fontSize: 12 }}
                target="_blank"
                rel="noreferrer"
                href="https://pay.kirvano.com/63026244-691e-4564-b5bd-05843bf119b7"
              >
                No possui acesso ? Clique aqui!
              </a>
            </CardBody>
            <CardFooter>
              <Button
                className="btn-fill"
                color="primary"
                type="submit"
                onClick={e => handleSubmit(e)}
              >
                Login
                {isSubmitting ? "..." : ""}
              </Button>
            </CardFooter>
          </Form>
        </Card>
      </Col>
    </div>
  );
}

export default Login;
