import React from "react";
// react plugin for creating notifications over the dashboard
import NotificationAlert from "react-notification-alert";
// connect requests with axios
import axios from "axios";

// reactstrap components
import {
  Card,
  CardHeader,
  CardBody,
  CardTitle,
  Row,
  Col,
  Label
} from "reactstrap";

// core components
import { chartExample2 } from "variables/charts.js";
import { LineChartView } from "backedComponents/LineChart/LineChartView";
import { SimpleTableView } from "backedComponents/SimpleTable/SimpleTableView";
import { Enviroments } from "../enviroments/enviroments";
import HistoryOdds from "../components/HistoryOdds/HistoryOdds";

import moment from "moment";

function Dashboard(props) {
  const dateNow = moment(new Date()).format("YYYY-MM-DD");
  const widthIframe = window.screen.width;

  const [
    analysisHistoryResultsAviator,
    setAnalysisHistoryResultsAviator
  ] = React.useState();
  const [
    analysisHistoryMostPositionsAndQuantity,
    setAnalysisHistoryMostPositionsAndQuantity
  ] = React.useState([0]);
  const [analysisHistoryByOdd, setAnalysisHistoryByOdd] = React.useState([0]);
  const [
    numberLastVelasFilterAnalysis,
    setNumberLastVelasFilterAnalysis
  ] = React.useState(10);
  const [analysisLoading, setAnalysisLoading] = React.useState(false);
  const [mediaState, setMedia] = React.useState({
    average: 0,
    averageBefore: 0
  });
  const [medianState, setMedian] = React.useState({
    median: 0,
    medianBefore: 0
  });

  const betHouse = "APOSTA_GANHA";

  const [analysisVela, setAnalysisVela] = React.useState(2);
  const [chartRef2, setChartRef2] = React.useState(null);
  /** ativar quando ocorrer erros no dashboard
    React.useEffect(() => {
      //
      //notify2("bc");
    }, []);
  */
  React.useEffect(() => {
    getMedianAndAverage();

    const interval = setInterval(() => {
      getMedianAndAverage();
    }, 5000);
    return () => clearInterval(interval);
  }, []);

  const getMedianAndAverage = async () => {
    try {
      const headers = {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("apiToken")}`
        }
      };
      const responseMedian = await axios.get(
        `${
          Enviroments.API_URL
        }analysis-calculation/median?date=${dateNow}&betHouse=${betHouse}&numberVelas=${10}&game=AVIATOR`,
        headers
      );
      const responseAverage = await axios.get(
        `${
          Enviroments.API_URL
        }analysis-calculation/average?date=${dateNow}&betHouse=${betHouse}&numberVelas=${10}&game=AVIATOR`,
        headers
      );

      if (responseMedian === medianState) {
        return;
      }

      setMedian(responseMedian.data);
      setMedia(responseAverage.data);
    } catch (error) {
      notify("bc");

      setTimeout(() => {
        localStorage.clear();
        window.location = "/auth/login";
      }, 5000);
      console.error(error);
    }
  };

  const getAnalysisHistoryVela = async () => {
    setAnalysisLoading(true);
    try {
      const headers = {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("apiToken")}`
        }
      };
      const url = `${Enviroments.API_URL}analysis-vela/odd-method?date=${dateNow}&odd-method=${analysisVela}&bet-house=${betHouse}`;
      const response = await axios.get(url, headers);

      const analysisHistoryResults = response.data.map(item => ({
        posicao: Number(item.quantityVelasBeforeBreakOdd),
        intervalo_tempo: item.intervalTime,
        odd: Number(item.breakOdd),
        minutes: Number(item.timeInMinutes),
        mediana: Number(item.median),
        media: Number(item.average)
      }));

      const analisysHistoryOdd = response.data
        .map(item => item.breakOdd)
        .slice(0, numberLastVelasFilterAnalysis);

      setAnalysisHistoryResultsAviator(analysisHistoryResults);
      setAnalysisHistoryByOdd(analisysHistoryOdd);
    } catch (error) {
      console.error(error);
    } finally {
      setAnalysisLoading(false);
    }
  };

  const getHistoryMostPositionsAnalysisHistoryVela = async () => {
    try {
      const headers = {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("apiToken")}`
        }
      };
      const url = `${Enviroments.API_URL}analysis-vela/odd-method/quantity-vela?date=${dateNow}&odd-method=${analysisVela}&bet-house=${betHouse}`;
      const response = await axios.get(url, headers);
      const analisysPositionsAndQde = response.data.map(item => ({
        posicao: Number(item.position),
        quantidade: Number(item.quantity),
        percentage: `${Number(item.percentage)}%`
      }));
      setAnalysisHistoryMostPositionsAndQuantity(analisysPositionsAndQde);
    } catch (error) {
      console.error(error);
    }
  };

  const notificationAlertRef = React.useRef(null);
  const notify = place => {
    var color = Math.floor(Math.random() * 5 + 1);
    var type;
    switch (color) {
      case 1:
        type = "primary";
        break;
      case 2:
        type = "success";
        break;
      case 3:
        type = "danger";
        break;
      case 4:
        type = "warning";
        break;
      case 5:
        type = "info";
        break;
      default:
        break;
    }
    var options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>
            <b>
              Se o problema for alertado muitas vezes, por favor deslogue e
              logue novamente. Ou podemos estar com alguma instabilidade. Se
              persistir por mais de 5 vezes, entre em contato com suporte.
            </b>
          </div>
        </div>
      ),
      type: "danger",
      icon: "tim-icons icon-bell-55",
      autoDismiss: 10
    };
    notificationAlertRef.current.notificationAlert(options);
  };

  const notifyWarning = React.useRef(null);
  const notify2 = place => {
    var color = Math.floor(Math.random() * 5 + 1);
    var type;
    switch (color) {
      case 1:
        type = "primary";
        break;
      case 2:
        type = "success";
        break;
      case 3:
        type = "danger";
        break;
      case 4:
        type = "warning";
        break;
      case 5:
        type = "info";
        break;
      default:
        break;
    }
    var options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>
            <b>
              O Dashboard est sendo atualizado! Previso da atualizao do
              histrico  at 18:00HS. Contamos com sua compreenso.
            </b>
          </div>
        </div>
      ),
      type: "danger",
      icon: "tim-icons icon-bell-55",
      autoDismiss: 100
    };
    notifyWarning.current.notificationAlert(options);
  };

  const handleClickSearchOdd = () => {
    getAnalysisHistoryVela();
    getHistoryMostPositionsAnalysisHistoryVela();
  };

  return (
    <div className="content">
      <div className="react-notification-alert-container">
        <NotificationAlert ref={notificationAlertRef} />
      </div>
      <Row>
        <Col xs="12">
          <HistoryOdds />
        </Col>
      </Row>
      <Row>
        <div className="react-notification-alert-container">
          <NotificationAlert ref={notifyWarning} />
        </div>

        <Col xs="6">
          <Card className="card-chart">
            <CardHeader>
              <Row>
                <Col sm="12">
                  <h5 className="card-category">Filtrada por: 10 casas</h5>
                  <Label>Mediana: </Label>
                  <CardTitle tag="h3">
                    {medianState.median.toFixed(2)}{" "}
                    <i
                      className={`tim-icons ${
                        medianState.median > medianState.medianBefore
                          ? "icon-minimal-up text-success"
                          : "icon-minimal-down text-danger"
                      }`}
                    />
                  </CardTitle>
                </Col>
              </Row>
            </CardHeader>
          </Card>
        </Col>
        <Col xs="6">
          <Card className="card-chart">
            <CardHeader>
              <Row>
                <Col sm="12">
                  <h5 className="card-category">Filtrada por: 10 casas</h5>
                  <Label>Media: </Label>
                  <CardTitle tag="h3">
                    {mediaState.average.toFixed(2)}{" "}
                    <i
                      className={`tim-icons ${
                        mediaState.average > medianState.averageBefore
                          ? "icon-minimal-up text-success"
                          : "icon-minimal-down text-danger"
                      }`}
                    />
                  </CardTitle>
                </Col>
              </Row>
            </CardHeader>
          </Card>
        </Col>
      </Row>
      <Row style={{ display: "block" }}>
        <Col md="12">
          <Card>
            <CardHeader>
              <h5 className="title">Aposta Ganha</h5>
              <p className="category">
                Pode operar diretamente pelo nosso Dashboard!
              </p>
            </CardHeader>
            <CardBody className="all-icons">
              <iframe
                height="700"
                title="Hacking Aviator"
                style={{ border: "none" }}
                width={widthIframe}
                name="aviator"
                className="col-md-12"
                src="https://external.afiliadosapostaganha.bet/"
                frameborder="0"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; 
                    gyroscope; picture-in-picture"
                allowfullscreen
              ></iframe>
            </CardBody>
          </Card>
        </Col>
      </Row>
      <Row className="mb-4">
        <Col sm="2">
          <Label>Selecione a ODD </Label>
          <div class="form-floating">
            <select
              id="floatingSelect"
              className="form-control"
              onChange={event => setAnalysisVela(event.target.value)}
              value={analysisVela}
            >
              <option value="2">2 X</option>
              <option value="3">3 X</option>
              <option value="5">5 X</option>
              <option value="10">10 X</option>
              <option value="25">25 X</option>
              <option value="50">50 X</option>
              <option value="100">100 X</option>
              <option value="200">200 X</option>
              <option value="500">500 X</option>
              <option value="1000">1000 X</option>
            </select>
          </div>
        </Col>
        <Col sm="2">
          <button
            className="btn btn-primary mt-4"
            onClick={handleClickSearchOdd}
          >
            Buscar
          </button>
        </Col>
      </Row>

      {analysisLoading && (
        <div className="d-flex justify-content-center align-items-center w-100">
          <div className="lds-spinner">
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
          </div>
        </div>
      )}

      {analysisHistoryResultsAviator === undefined ? (
        <>Clique no boto buscar para realizar sua pesquisa</>
      ) : (
        <Row>
          <Col lg="12">
            <Card className="card-chart">
              <CardHeader>
                <h5 className="card-category">
                  Grfico de Analise de Vela:{" "}
                  <strong className="text-primary">{analysisVela}X</strong>
                </h5>

                <LineChartView
                  data={chartExample2(analysisHistoryByOdd).data}
                  options={chartExample2(analysisHistoryByOdd).options}
                  passedRef={newRef => setChartRef2(newRef)}
                  totalData={analysisHistoryByOdd.length}
                ></LineChartView>
              </CardHeader>
            </Card>
          </Col>
          <Col lg="8">
            <Card className="card-chart">
              <CardHeader>
                <h5 className="card-category">
                  Histrico Detalhado de Analise de Vela
                </h5>
                <h5>
                  Vela:{" "}
                  <strong className="text-primary">{analysisVela}X</strong>
                </h5>
                <CardTitle tag="h3">
                  <SimpleTableView
                    dataHeaders={[
                      "posicao",
                      "intervalo_tempo",
                      "odd",
                      "minutes",
                      "mediana",
                      "media"
                    ]}
                    data={analysisHistoryResultsAviator}
                  />
                  <i className="tim-icons icon-send text-primary" />{" "}
                </CardTitle>
              </CardHeader>
            </Card>
          </Col>
          <Col lg="4">
            <Card className="card-chart">
              <CardHeader>
                <h5 className="card-category">
                  Posicao que repetiu o mesmo valor da ODD:{" "}
                  <strong className="text-primary">{analysisVela}X</strong>
                </h5>
                <CardTitle tag="h3">
                  <SimpleTableView
                    dataHeaders={["posicao", "quantidade", "percentage"]}
                    data={analysisHistoryMostPositionsAndQuantity}
                  />
                </CardTitle>
              </CardHeader>
            </Card>
          </Col>
        </Row>
      )}
    </div>
  );
}

export default Dashboard;
