import React from "react";
// connect requests with axios
import axios from "axios";

import Switch from "react-switch";
import { Puff } from "react-loader-spinner";
import NotificationAlert from "react-notification-alert";

// reactstrap components
import { Card, CardHeader, CardBody, CardTitle, Row, Col } from "reactstrap";

// core components
import { Enviroments } from "../../enviroments/enviroments";
import aviatorLogo from "assets/img/aviator.png";
import pauseIcon from "assets/img/pause-icon.png";

import moment from "moment";

function HistoryOdds(props) {
  const dateNow = moment(new Date()).format("YYYY-MM-DD");

  const defaultHistory = [
    {
      odd: 0,
      time: 10
    }
  ];

  const defaultHistory2 = [
    {
      odd: 2,
      time: 13
    }
  ];

  const [historyResultsAviator, setHistoryResultsAviator] = React.useState(
    defaultHistory
  );

  const [historyResultsAviator2, setHistoryResultsAviator2] = React.useState(
    defaultHistory2
  );

  const [signalConfirm, setSignalConfirm] = React.useState(false);
  const [gale1, setGale1] = React.useState(false);
  const [gale2, setGale2] = React.useState(false);

  const [historyLoading, setHistoryLoading] = React.useState(true);
  const [numberVelas, setNumberVelas] = React.useState(10);
  const [valueOfVelas, setValueOfVelas] = React.useState(0);
  const [betHouse, setBetHouse] = React.useState("APOSTA_GANHA");
  const [isActiveOdd, setActiveOdd] = React.useState(false);
  const [lastOdd, setLastOdd] = React.useState(0);
  const [hackingOdds, setHackingOdds] = React.useState({
    sinal: false,
    gale1: false,
    gale2: false,
    red: false,
    green: false,
    odd: 0,
    oddSinal: 0
  });

  const getBadgeClassName = value => {
    if (value < 2) {
      return "bg-info";
    } else if (value <= 9.99) {
      return "bg-purple";
    } else if (value >= 10) {
      return "bg-primary";
    }
  };

  React.useEffect(() => {
    const interval = setInterval(async () => {
      const res = await getHistoryResultsAviator();
      const newResult = res.filter(item => item.odd > valueOfVelas);
      setHistoryResultsAviator(newResult);
    }, 3000);
    return () => clearInterval(interval);
  }, [numberVelas, valueOfVelas]);

  const getHistoryResultsAviator = async () => {
    try {
      const headers = {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("apiToken")}`
        }
      };
      const response = await axios.get(
        `${Enviroments.API_URL}history-vela?date=${dateNow}&bet-house=${betHouse}&number-velas=${numberVelas}`,
        headers
      );
      return response.data.velas;
    } catch (error) {
      console.error(error);
    } finally {
      setHistoryLoading(false);
    }
  };

  const handleToogleSearchOdds = e => {
    setActiveOdd(e);
  };

  React.useEffect(() => {
    const getHistory = async () => {
      let res = defaultHistory2;
      // console.log(historyResultsAviator2[0].odd, historyResultsAviator[0].odd);

      if (historyResultsAviator2[0].odd === historyResultsAviator[0].odd) {
        return;
      }

      if (
        isActiveOdd &&
        historyResultsAviator2[0].odd !== historyResultsAviator[0].odd
      ) {
        // console.log( `${historyResultsAviator2[0].odd}  diferente de ${historyResultsAviator[0].odd}`);

        const game1 = historyResultsAviator[0].odd;
        const game2 = historyResultsAviator[1].odd;
        const game3 = historyResultsAviator[2].odd;

        // GALE 2
        if (
          hackingOdds.sinal &&
          hackingOdds.gale1 &&
          hackingOdds.gale2 &&
          game1 > 1.5
        ) {
          // console.log("DEU GREEN NO GALE 2", hackingOdds);
          const hacking = {
            ...hackingOdds,
            sinal: false,
            gale1: false,
            gale2: false,
            odd: game1
          };
          notify(
            "tc",
            "success",
            `DEUUUU GREEEEEN ${game1}, NO GALE 2! UFAAAA.`
          );
          setHackingOdds(hacking);
        } else if (hackingOdds.sinal && gale1 && gale2) {
          const hacking = {
            ...hackingOdds,
            sinal: false,
            gale1: false,
            gale2: false,
            odd: game1
          };
          setHackingOdds(hacking);
          // console.log("DEU RED", hackingOdds);
        }

        if (
          hackingOdds.sinal &&
          hackingOdds.gale1 &&
          !hackingOdds.gale2 &&
          game1 > 1.5
        ) {
          // console.log("DEU GREEN NO GALE 1", hackingOdds);
          notify(
            "tc",
            "success",
            `DEUUUU GREEEEN ${game1} NO GALE 1, REXXXXXPEITA!`
          );
          const hacking = {
            ...hackingOdds,
            sinal: false,
            gale1: false,
            gale2: false,
            odd: game1
          };
          setHackingOdds(hacking);
        } else if (hackingOdds.sinal && hackingOdds.gale1) {
          const hacking = {
            ...hackingOdds,
            sinal: true,
            gale1: true,
            gale2: true,
            odd: game1
          };
          setHackingOdds(hacking);
          notify(
            "tc",
            "warning",
            "ESTAMOS NO ULTIMO GALE TROPA! POSITIVIDADE! BORAAAAA"
          );
          // console.log("ESTAMOS NO GALE 2", hackingOdds);
        }

        // SEM GALE
        if (
          hackingOdds.sinal &&
          !hackingOdds.gale1 &&
          !hackingOdds.gale2 &&
          game1 > 1.5
        ) {
          // console.log("DEU GREEN SEM GALE", hackingOdds);
          const hacking = {
            ...hackingOdds,
            sinal: false,
            gale1: false,
            gale2: false,
            odd: game1
          };
          notify(
            "tc",
            "success",
            `DEU GREEEEEN ${game1}X SEM GALEEEE! RESPEITA TROPINHA!`
          );
          setHackingOdds(hacking);
        } else if (
          hackingOdds.sinal &&
          !hackingOdds.gale1 &&
          !hackingOdds.gale2
        ) {
          const hacking = {
            ...hackingOdds,
            gale1: true,
            odd: game1
          };
          setHackingOdds(hacking);
          notify("tc", "warning", "ESTAMOS NO GALE 1, BORAAAAAAAA!");
          // console.log("ESTAMOS NO GALE 1", hackingOdds);
        }

        let media = historyResultsAviator
          .slice(0, 9)
          .map(x => x.odd)
          .reduce(function(total, item, indice, array) {
            total = total + item;

            if (indice == array.length - 1) {
              return total / array.length;
            }

            return total;
          });

        if (media) {
          media = media.toFixed(2);
        }

        console.log(media);

        // SINAL CONFIRMADO
        if (
          (game1 < 2 && game2 < 1.5 && media > 2) ||
          (game1 >= 2 && game2 < 2) ||
          (game1 > 2 &&
            game1 < 12 &&
            game2 > 2 &&
            game2 < 12 &&
            game3 > 2 &&
            game3 < 12) ||
          !hackingOdds.sinal
        ) {
          // console.log("SINAL CONFIRMADO", game1);
          notify(
            "tc",
            "warning",
            `SINAL CONFIRMADO, PRA CIMA TROPINHA! ENTRAR APS ${game1}X, PROTEO 1.50X`
          );
          const searchOdd = {
            ...hackingOdds,
            sinal: true,
            odd: game1,
            oddSinal: game1
          };
          setLastOdd(1.5);
          setHackingOdds(searchOdd);
        }
      }

      setHistoryResultsAviator2(historyResultsAviator);
    };
    getHistory();
  }, [isActiveOdd, historyResultsAviator, historyResultsAviator2]);

  const notificationAlertRef = React.useRef(null);
  const notify = (place, color, message) => {
    var options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>
            <b>{message}</b>
          </div>
        </div>
      ),
      type: color,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 8
    };
    notificationAlertRef.current.notificationAlert(options);
  };

  return (
    <>
      <div className="history-odd">
        <div className="react-notification-alert-container">
          <NotificationAlert ref={notificationAlertRef} />
        </div>
        {historyLoading ? (
          <div className="d-flex justify-content-center align-items-center w-100">
            <div className="lds-spinner">
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
            </div>
          </div>
        ) : (
          <>
            <Row>
              <Col xs="12">
                <Card className="card-chart">
                  <CardHeader>
                    <Row>
                      <Col className="text-left" sm="4">
                        <h5 className="card-category">
                          Histrico de velas do Aviator
                        </h5>
                        <CardTitle tag="h3">Histrico de Velas</CardTitle>
                      </Col>
                      <Col
                        sm="2"
                        className="d-flex justify-content-start align-items-center"
                      >
                        <div class="form-floating">
                          Qde
                          <select
                            id="floatingSelect"
                            className="form-control"
                            onChange={event =>
                              setNumberVelas(event.target.value)
                            }
                            value={numberVelas}
                          >
                            <option value="10">10</option>
                            <option value="50">50</option>
                            <option value="100">100</option>
                            <option value="200">200</option>
                            <option value="500">500</option>
                            <option value="1000">1000</option>
                          </select>
                        </div>{" "}
                      </Col>
                      <Col
                        sm="2"
                        className="d-flex justify-content-start align-items-center"
                      >
                        <div class="form-floating">
                          Filtro
                          <select
                            id="floatingSelect"
                            className="form-control"
                            onChange={event =>
                              setValueOfVelas(event.target.value)
                            }
                            value={valueOfVelas}
                          >
                            <option value="0">Resetar</option>
                            <option value="10">10</option>
                            <option value="20">20</option>
                            <option value="30">30</option>
                            <option value="50">50</option>
                            <option value="100">100</option>
                          </select>
                        </div>{" "}
                      </Col>
                    </Row>
                  </CardHeader>
                  <CardBody>
                    <div className="history-results row">
                      {historyResultsAviator.map((item, i) => (
                        <div className="vela-results" key={item + i}>
                          <span
                            key={item.time}
                            className={`badge text-wrap ${getBadgeClassName(
                              item.odd
                            )}`}
                            style={{
                              color: "white",
                              backgroundColor:
                                getBadgeClassName(item.odd) === "bg-purple"
                                  ? "#5603ad"
                                  : ""
                            }}
                          >
                            {item.odd && item.odd.toFixed(2)}
                          </span>

                          <br />

                          <span style={{ fontSize: 11 }}>
                            {moment(item.time).format("HH:mm:ss")}
                          </span>
                        </div>
                      ))}
                    </div>
                  </CardBody>
                </Card>
              </Col>
            </Row>
          </>
        )}
      </div>
    </>
  );
}

export default HistoryOdds;
